import React from 'react';
import { Helmet } from 'react-helmet';
import '../../App.css';
import { Container, Row } from 'react-bootstrap';

function DronePiloting() {
  return (
    <>
      <Helmet>
        <title>School of Engineering | Be Part of Xtra</title>
        <meta
          name="description"
          content="Ngee Ann Polytechnic SoE Building a better world through a broad-based multidisciplinary approach"
        />
      </Helmet>
      <Container fluid className="header-wrap">
        <Row className="dronepilot-header-banner-logo">
          <Container className="dronepilot-header-banner-text">
            <h1>Drone Piloting</h1>
          </Container>
        </Row>
        <Row className="dronepilot-intro">
          <Container className="dronepilot-intro-header">
            <h1><b>Introduction to Drone Piloting Videography</b></h1>
          </Container>
          <Container className="dronepilot-course-info">
            <h1><b>This is a one day recreational drone piloting. You get to learn basic drone piloting skill,
            CAAS drone regulations (activities permit and pilot license), framing of photos and video
            and be able to learn how to take video by making your drone to follow you running/walking path.
                <br></br>You will also learn to make semi-professional aerial video taking from high altitude as well as
                 circulating with camera always aiming to record you in the middle of the screen.</b></h1><hr></hr>
            <h2><b>Participants will:</b></h2>
            <p>1. Attain skills to take high quality photos and videos for personal and work use</p>
            <p>2. Gain knowledge of Drones, definition, functions, settings and more</p>
            <p>3. Learn basic framing of photography and video</p>
            <p>4. Assess how to choose your first drone</p>
            <p>5. Learn to pilot a drone safely</p><hr></hr>
            <div className="drone-image"></div><hr></hr>
            <h3><b>Who should be attending?</b></h3>
            <h4>Anyone <span>above 16 years old</span> to fly drone for recreation.</h4>
            <div className="drone-recreate-facts">
              <h1><b><u>For recreational purpose, learn basic framing/angles</u></b></h1>
              <p><b>- Non-commercial wedding shooting</b></p>
              <p><b>-	Non-commercial photo and video taking</b></p>
              <p><b>-	Controlling gimbal and camera setting</b></p>
              <p><b>-	Follow-me drone setting</b></p>
              <p><b>-	Basic drone video editing</b></p><hr></hr>
              <h2>***Our next run of our Drone CET course will take place between June and December 2021***</h2>
            </div>

          </Container>

        </Row>

        <a href="https://www.np.edu.sg/lifelonglearning/Pages/Introduction-Drone-Piloting-Photo-Videography.aspx" target="_blank" rel="noreferrer"><Row className="dronepilot-signup-link">
          <Container className="drone-signup-link">
            <h1>Click Here to sign up for our Drone CET course now!
              </h1>
          </Container>
        </Row></a>
      </Container>
    </>

  )

}

export default DronePiloting
