import React, { useState } from 'react';
import { Helmet } from 'react-helmet';
import { Container, Row, Col, Carousel } from 'react-bootstrap';
import '../../App.css';
import YouTube from 'react-youtube';
import tnf from '../../images/top-notch-facilities.webp';
import tnfjpg from '../../images/top-notch-facilities.jpg';
import oe from '../../images/overseas-experience.webp';
import oejpg from '../../images/overseas-experience.jpg';
import op from '../../images/orientation-programme.webp';
import opjpg from '../../images/orientation-programme.jpg';
import yep from '../../images/youth-expedition-project.webp';
import yepjpg from '../../images/youth-expedition-project.jpg'
import ip from '../../images/immersion-programmes.webp';
import ipjpg from '../../images/immersion-programmes.jpg';
import st from '../../images/study-trips.webp';
import stjpg from '../../images/study-trips.jpg'
import servicelearning from '../../images/service-learning.webp';
import servicelearningjpg from '../../images/service-learning.jpg';
import lucas from '../../images/lucas-leong.webp';
import lucasjpg from '../../images/lucas-leong.jpg'
import qixuan from '../../images/qi-xuan-tai.webp';
import qixuanjpg from '../../images/qi-xuan-tai.jpg';
import rangerslogo from '../../images/rangerslogo.webp';
import rangerslogojpg from '../../images/rangerslogo.jpg';

function StudentLife() {
    
    const opts = {
        height: '400',
        width: '640',
        playerVars: {
            // https://developers.google.com/youtube/player_parameters
            modestbranding: 1,
            autoplay: 0,
        },
    }

    const [index, setIndex] = useState(0);

    const handleSelect = (selectedIndex, e) => {
        setIndex(selectedIndex);
    };
    let orientationweb = "https://www.np.edu.sg/studentlife/Pages/SoEOrientation.aspx";
    let soeweb = "https://www.np.edu.sg/soe/Pages/default.aspx";
    let overseas = "https://www.np.edu.sg/travelandlearn/Pages/default.aspx";
    let svclearning = "https://www.np.edu.sg/servicelearning/Pages/overseas.aspx";
    let soerangersweb = "http://soerangers.com/";
    return (
        <>
            <Helmet>
                <title>Student Life @ SoE | Ngee Ann Polytechnic</title>
                <meta
                    name="description"
                    content="Ngee Ann Polytechnic SoE Broad-based multidisciplinary approach Student Life Orientation Overseas"
                />
            </Helmet>
            <Container fluid className="header-wrap">
                <Row className="studentlife-header-banner-logo">
                    <Container className="studentlife-header-banner-text">
                        <h1>Life @ SoE</h1>
                    </Container>
                </Row>
                <Row className="student-life-body">
                    <Col xs={12} md={12} className="explore">
                        <Col xs={12} md={7} className="explore-circle-container">
                            <div className="orientation-programme-circle">
                                <picture>
                                    <source srcSet={op} type="image/webp"/>
                                    <source srcSet={opjpg} type="image/jpeg"/>
                                    <img src={op} className="orientation-programme-img" alt="orientation programme"/>
                                </picture>
                                <a href={soeweb} target="_blank" rel="noreferrer">
                                    <div className="circle-text orientation-programme-text">
                                        <p>Get energized and ready as you transition with funfilled orientation programmes organized by our students.</p>
                                        <p>Find out more about SoE</p>
                                    </div>
                                </a>
                            </div>
                            <div className="overseas-experience-circle">
                                <picture>
                                    <source srcSet={oe} type="image/webp"/>
                                    <source srcSet={oejpg} type="image/jpeg"/>
                                    <img src={oe} className="overseas-experience-img" alt="overseas experience"/>
                                </picture>
                                <a href={soeweb} target="_blank" rel="noreferrer">
                                    <div className="circle-text overseas-experience-text">
                                        <p>Opportunities for overseas experience are offered to every student here in Ngee Ann Polytechnic's School of Engineering</p>
                                        <p>Find out more about SoE</p>
                                    </div>
                                </a>
                            </div>
                            <div className="top-notch-facilities-circle">
                                <picture>
                                    <source srcSet={tnf} type="image/webp"/>
                                    <source srcSet={tnfjpg} type="image/jpeg"/>
                                    <img src={tnf} className="top-notch-facilities-img" alt="top notch facilities"/>
                                </picture>
                                <a href={soeweb} target="_blank" rel="noreferrer">
                                    <div className="circle-text top-notch-facilities-text">
                                        <p>Offering many top-notch facilities here in Ngee Ann Polytechnic's School of Engineering.</p>
                                        <p>Find out more about SoE</p>
                                    </div>
                                </a>
                            </div>
                        </Col>
                        <Col xs={12} md={5} className="explore-container">
                            <h2>Explore</h2>
                            <ul className="explore-container-group">
                                <li className="explore-container-item">
                                    <a href={orientationweb} target="_blank" rel="noreferrer"><h3 className="explore-item-title">Orientation Programmes</h3></a>
                                    <p className="explore-item-content">
                                        Get energized and ready as you transition with funfilled orientation programmes organized by our students.
                                    </p>
                                    <p className="explore-item-content">
                                        Begin your <a href={orientationweb} target="_blank" rel="noopener noreferrer"><strong><span className="highlight">#XTRAORDINARY</span></strong></a> Journey At NP.
                                    </p>
                                   
                                </li>
                                <hr/>
                                <li className="explore-container-item">
                                    <a href={overseas} target="_blank" rel="noreferrer"><h3 className="explore-item-title">Overseas Experiences</h3></a>
                                    <p className="explore-item-content">
                                        Opportunities for overseas experience are offered to every student here in Ngee Ann Polytechnic's School of Engineering
                                    </p>
                                    <p className="explore-item-content">
                                        Learn from engineering greats through Overseas trips and Site Visits.
                                    </p>
                                </li>
                                <hr/>
                                <li className="explore-container-item">
                                    <a href={soeweb} target="_blank" rel="noreferrer"><h3 className="explore-item-title">Top-Notch Facilities</h3></a>
                                    <p>Offering many top-notch facilities here in Ngee Ann Polytechnic's School of Engineering.</p>
                                    <p className="explore-item-content">
                                        Visit our Aerospace Hub, Innovation Labs, Production Studios and More.
                                    </p>
                                </li>
                                <hr/>
                            </ul>
                        </Col>
                    </Col>
                    <Col xs={12} md={12} className="fop">
                        <Col xs={12} md={7} className="fop-image">
                            <YouTube videoId="g2VVGoeUiaU" className="responsive-iframe" containerClassName="video" opts={opts} />
                        </Col>
                        <Col xs={12} md={5} className="fop-container">
                            <h2>Freshmen Orientation Programme</h2>
                            <p>Feel refreshed and welcomed to Ngee Ann Polytechnic's School of Engineering with our specially curated Freshmen Orientation Programme hosted by our students.</p>
                            <p>Meet new people from various engineering courses as well as seniors that will guide you along the way.</p>
                            <p>Be ready to kick-start your polytechnic journey with our <a href={orientationweb} target="_blank" rel="noopener noreferrer"><strong><span className="highlightf">Freshmen Orientation Programme</span></strong></a>.</p>
                        </Col>
                    </Col>
                    <Col xs={12} md={12} className="go-beyond">
                        <Col xs={12} md={8} className="go-beyond-circle-container">
                            <div className="study-trips-circle">
                                <picture>
                                    <source srcSet={st} type="image/webp"/>
                                    <source srcSet={stjpg} type="image/jpeg"/>
                                    <img src={st} className="study-trips-img" alt="study trips"/>
                                </picture>
                                <a href={soeweb} target="_blank" rel="noreferrer">
                                    <div className="circle-text study-trips-text">
                                        <p>Visit workplaces and companies, discover the different industries of engineering</p>
                                        <p>Find out more about SoE</p>
                                    </div>
                                </a>
                            </div>
                            <div className="immersion-programmes-circle">
                                <picture>
                                    <source srcSet={ip} type="image/webp"/>
                                    <source srcSet={ipjpg} type="image/jpeg"/>
                                    <img src={ip} className="immersion-programmes-img" alt="immersion programmes"/>
                                </picture>
                                <a href={soeweb} target="_blank" rel="noreferrer">
                                    <div className="circle-text immersion-programmes-text">
                                        <p>Go beyond classrooms and explore through our immersion programmes</p>
                                        <p>Find out more about SoE</p>
                                    </div>
                                </a>
                            </div>
                            <div className="youth-expedition-project-circle">
                                <picture>
                                    <source srcSet={yep} type="image/webp"/>
                                    <source srcSet={yepjpg} type="image/jpeg"/>
                                    <img src={yep} className="youth-expedition-project-img" alt="youth expedition project"/>
                                </picture>
                                <a href={svclearning} target="_blank" rel="noreferrer">
                                    <div className="circle-text youth-expedition-project-text">
                                        <p>Participate in the youth expedition project by the National Youth Council and visit developing nations</p>
                                        <p>Find out more about YEP</p>
                                    </div>
                                </a>
                            </div>
                        </Col>
                        <Col xs={12} md={4} className="go-beyond-container">
                            <h2>Go Beyond</h2>
                            <ul className="go-beyond-container-group">
                                <li className="go-beyond-container-item">
                                    <a href={overseas} target="_blank" rel="noreferrer"><h3 className="go-beyond-item-title">Study Trips</h3></a>
                                    <p className="go-beyond-item-content">
                                        Gain exposure to industries related to your disciplines.
                                    </p>
                                    <p className="go-beyond-item-content">
                                        Visit workplaces and companies, truly discover the different industries of engineering
                                    </p>
                                    <hr/>
                                </li>
                                <li className="go-beyond-container-item">
                                    <a href={overseas} target="_blank" rel="noreferrer"><h3 className="go-beyond-item-title">Immersion Programmes</h3></a>
                                    <p className="go-beyond-item-content">
                                        Go beyond classrooms and explore through our immersion programmes.
                                    </p>
                                    <p className="go-beyond-item-content">
                                        Gain greater appreciation of different cultures.
                                    </p>
                                    <hr/>
                                </li>
                                <li className="go-beyond-container-item">
                                    <a href={svclearning} target="_blank" rel="noreferrer"><h3 className="go-beyond-item-title">Expeditions</h3></a>
                                    <p className="go-beyond-item-content">
                                        Serve the community and develop solutions.
                                    </p>
                                    <p className="go-beyond-item-content">
                                        Participate in the youth expedition project by the National Youth Council and visit developing nations
                                    </p>
                                    <hr/>
                                </li>
                            </ul>
                        </Col>
                    </Col>
                    <Col xs={12} md={12} className="service-learning">
                        <Col xs={12} md={6} className="service-learning-image">
                            <a href={svclearning} target="_blank" rel="noreferrer">
                                <picture>
                                    <source srcSet={servicelearning} type="image/webp"/>
                                    <source srcSet={servicelearningjpg} type="image/jpeg"/>
                                    <img src={servicelearning} className="service-learning-img" alt="service-learning"/>
                                </picture>
                            </a>
                        </Col>
                        <Col xs={12} md={6} className="service-learning-content">
                            <h2>Service Learning</h2>
                            <p>Service Learning plays a crucial role in nuturing every Ngee Ann graduate as we aspire students to have a big heart for the community, by sharing the new insights and skills picked up from the classrooms. Encouraging passionate learners to strive for lifelong learning and making great impacts to our community with service learning in order to build the social future of tomorrow.</p>
                        </Col>
                    </Col>
                    <Carousel activeIndex={index} onSelect={handleSelect}>
                        <Carousel.Item>
                            <Col xs={12} md={12} className="student-experiences">
                                <Col xs={12} md={6} className="student-experiences-content">
                                    <h2>Hear from our graduates</h2>
                                    <p>"SoE has provided me with many inspirational experiences and valuable opportunities during my time in NP.
        My Final Year Project exposed me to the world of research and problem solving using emerging technologies. I learned about dielectric elastomer actuators and their potential to replace traditional actuators due to their simplicity and low cost. It took some time to understand the principles behind this new technology, but under the guidance of an NUS professor and his PhD students, my project partner and I successfully constructed a working prototype actuator that the professor envisioned.
        Unique to Engineering Science is the Berlin study trip which allowed me to visit distinguished universities and research establishments to learn about the leading developments in different technology research fields. From 3D-printing metal to game design, it was an exciting and enriching addition to my polytechnic curriculum.
        On top of such opportunities, SoE has state-of-the-art laboratories and workshops which give us engaging practical lessons and greater capability in fabricating components for our school projects. Looking back, SoE provided me with a quality education that included a multitude of enriching experiences which I am sure to remember."</p>
                                </Col>
                                <Col xs={12} md={6} className="student-experiences-image">
                                    <picture>
                                        <source srcSet={lucas} type="image/webp"/>
                                        <source srcSet={lucasjpg} type="image/jpeg"/>
                                        <img src={lucas} className="lucas-leong-img" alt="lucas leong"/>
                                    </picture>
                                    <h3>Lucas Leong</h3>
                                    <p>Graduate of Class of 2019</p>
                                    <p>Engineering Science</p>
                                </Col>
                            </Col>
                        </Carousel.Item>
                        <Carousel.Item>
                            <Col xs={12} md={12} className="student-experiences">
                                <Col xs={12} md={6} className="student-experiences-content">
                                    <h2>Hear from our graduates</h2>
                                    <p>"It was really a enriching experience throughout my 3 years in the School of Engineering. I have learnt a lot throughout these 3 years with the opportunities given to me to participate in the overseas immersion programme, allowing me to gain experience in studying abroad and immerse myself in their culture. Additionally, I had the chance to intern in a medical technology company in my final year, where I gained insightful experiences of the biomedical engineering industry. Other than curriculum based activities, I was also part of Rangers where I developed more on my communication skills and leadership skills. All in all, my 3 years in NP had provided me with valuable lessons."</p>
                                </Col>
                                <Col xs={12} md={6} className="student-experiences-image qx">
                                    <picture>
                                        <source srcSet={qixuan} type="image/webp"/>
                                        <source srcSet={qixuanjpg} type="image/jpeg"/>
                                        <img src={qixuan} className="qi-xuan-tai-img" alt="tai qi xuan"/>
                                    </picture>
                                    <h3>Tai Qi Xuan</h3>
                                    <p>Graduate of Class of 2020</p>
                                    <p>Biomedical Engineering</p>
                                    <p>Current Student at Nanyang Technological University</p>
                                </Col>
                            </Col>
                        </Carousel.Item>
                    </Carousel>
                    <Col xs={12} md={12} className="rangers">
                        <Col xs={12} md={6} className="rangers-circle-container">
                            <div className="circle rangers-circle"></div>
                            <picture>
                                <source srcSet={rangerslogo} type="image/webp"/>
                                <source srcSet={rangerslogojpg} type="image/jpeg"/>
                                <img src={rangerslogo} className="rangers-logo-img" alt="rangers logo"/>
                            </picture>
                        </Col>
                        <Col xs={12} md={6} className="rangers-container">
                            <h2>School of Engineering Rangers</h2>
                            <h3>
                                <span className="highlightr">UNITY</span> | <span className="highlightr">PROFESSIONALISM</span> | <span className="highlightr">ENTHUSIASM</span>
                            </h3>
                            <br/>
                            <p>
                                Find out more about our friendly rangers from the school of engineering as they host various exciting outreach events throughout the year for secondary school students and the general public such as our annual Open House and Red Camp.
                            </p>
                            <p>
                                Bond and work as a team in professional settings, promoting the good vibes of school of engineering.
                            </p>
                            <p>
                                Hone your skills of public speaking, events management, and many more with <a href={soerangersweb} target="_blank" rel="noreferrer"><strong><span className="highlightred">SoE Rangers</span></strong></a>.
                            </p>
                        </Col>
                    </Col>
                    <Col xs={12} md={12} className="social-feed">
                        
                    </Col>
                </Row>
            </Container>
        </>
    )
}

export default StudentLife
