import React, { Component } from 'react';
import './ContentBoxesHomePage.css';
import engineerIconWhite2 from "../PNGFolder/engineerIconWhite2.webp";
import engineerIconWhite2png from "../PNGFolder/engineerIconWhite2.png";
import studentLIfeWhite from "../PNGFolder/studentLIfeWhite.webp";
import studentLIfeWhitepng from "../PNGFolder/studentLIfeWhite.png";
import coursesLogoWhite from "../PNGFolder/coursesLogoWhite.webp";
import coursesLogoWhitepng from "../PNGFolder/coursesLogoWhite.png";
import { Container, Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom'

export class ContentBoxesHomePage extends Component {
    render() {
        return (
            <Row className="BoxesContainer">
                <Col xs={12} sm={12} md={4} className="BoxOne">
                    <Link to="/projects">
                        <Container className="BoxOneContent">
                            <picture>
                                <source srcSet={engineerIconWhite2} type="image/webp"/>
                                <source srcSet={engineerIconWhite2png} type="image/png"/>
                                <img src={engineerIconWhite2} className="boxOneLogo" alt="projectlogo"/>
                            </picture>
                            <h3 className="projectTitle">PROJECTS</h3>
                        </Container>
                    </Link>
                </Col>
                <Col xs={12} sm={12} md={4} className="BoxTwo">
                    <Link to="/studentlife">
                        <Container className="BoxTwoContent">
                            <picture>
                                <source srcSet={studentLIfeWhite} type="image/webp"/>
                                <source srcSet={studentLIfeWhitepng} type="image/png"/>
                                <img src={studentLIfeWhite} className="boxTwoLogo" alt="lifeatsoelogo"/>
                            </picture>
                            <h3 className="studentLifeTitle">LIFE @ SOE</h3>
                        </Container>
                    </Link>
                </Col>
                <Col xs={12} sm={12} md={4} className="BoxThree">
                    <Link to="/courses">
                        <Container className="BoxThreeContent">
                            <picture>
                                <source srcSet={coursesLogoWhite} type="image/webp"/>
                                <source srcSet={coursesLogoWhitepng} type="image/png"/>
                                <img src={coursesLogoWhite} className="boxThreeLogo" alt="courseslogo"/>
                            </picture>
                            <h3 className="coursesTitle">COURSES</h3>
                        </Container>
                    </Link>
                </Col>
            </Row>
        )

    }

}

export default ContentBoxesHomePage;