import React from 'react';
import { Helmet } from 'react-helmet';
import '../../App.css';
import Carousel from 'react-bootstrap/Carousel';
import MobileBoxDisplay from "../MobileBoxDisplayComponents/MobileBoxDisplay";
import ContainerHomePage from "../ContainerHomePage/ContainerHomePage";
import ContentBoxesHomePage from "../ContentBoxesHomePage/ContentBoxesHomePage";
import ImageHover from "../ImageHoverHomePage/ImageHover";
import { Link } from 'react-router-dom';
import YouTube from 'react-youtube';

//Images//
import i1 from "../../images/i1.webp";
import i1jpg from "../../images/i1.jpg";
import i3 from "../../images/i3.webp";
import i3jpg from "../../images/i3.jpg";

//SCSS file for BootStrap React Carousel//
import "../Carousel/bootstrapCarousel.scss";

  function Home() {
    const opts = {
      height: '400',
      width: '640',
      playerVars: {
          // https://developers.google.com/youtube/player_parameters
          modestbranding: 1,
          autoplay: 1,
      },
  }

  return (
    <>
      <Helmet>
        <title>School of Engineering | Ngee Ann Polytechnic</title>
        <meta
          name="description"
          content="Ngee Ann Polytechnic SoE Building a better world through a broad-based multidisciplinary approach"
        />
      </Helmet>
      <Carousel bsPrefix='carousel' className="custom-carousel">
        <Carousel.Item bsPrefix='carousel-item' className="custom-item">
          <YouTube videoId="RfwRqOVYrcs" className="responsive-iframe" containerClassName="video" opts={opts} />
          <Carousel.Caption bsPrefix='carousel-caption' className="custom-caption-color" >
            <div className="carouselBox">
              <h3 className="SecondSlide">SOE in a Nutshell</h3>
              <p className="SecondSlidePara">Find out more about SOE and our learning process.</p>
              <Link to="/studentlife"><button className="carousel-inside-box">LEARN MORE</button></Link>
            </div>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item bsPrefix='carousel-item' className="custom-item">
          <picture>
              <source srcSet={i1} type="image/webp"/>
              <source srcSet={i1jpg} type="image/jpeg"/>
              <img
                className="d-block w-100"
                src={i1}
                alt="Second slide"
              />
          </picture>
          <Carousel.Caption bsPrefix='carousel-caption' className="custom-caption-color">
            <div className="carouselBox">
              <h3 className="FirstSlide">World Skills 2019</h3>
              <p className="FirstSlidePara">Find out more about World Skills 2019 (Mechatronics) in their competition in Russia</p>
              <Link to="/news"><button className="carousel-inside-box">LEARN MORE</button></Link>
            </div>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item bsPrefix='carousel-item' className="custom-item">
          <picture>
              <source srcSet={i3} type="image/webp"/>
              <source srcSet={i3jpg} type="image/jpeg"/>
              <img
                className="d-block w-100"
                src={i3}
                alt="Third slide"
              />
          </picture>
          <Carousel.Caption bsPrefix='carousel-caption' className="custom-caption-color">
            <div className="carouselBox">
              <h3 className="ThirdSlide">World Skills 2019</h3>
              <p className="ThirdSlidePara">Find out more about World Skills 2019 (Mechatronics) in their competition in Russia</p>
              <Link to="/news"><button className="carousel-inside-box">LEARN MORE</button></Link>
            </div>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
      <MobileBoxDisplay />
      <ContainerHomePage />
      <ContentBoxesHomePage />
      <ImageHover />
    </>
  )
}

export default Home
