import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import ScrollToTop from './components/pages/ScrollToTop';
import ScrollTopArrow from "./components/ScrollToTop/scrollTopArrow";
import Navbar from './components/Navbar';
import Home from './components/pages/Home';
import About from './components/pages/About';
import Courses from './components/pages/Courses';
import StudentLife from './components/pages/StudentLife';
import Drone from './components/pages/Drone';
import DronePiloting from './components/pages/DronePiloting';
import Web from './components/pages/CreateWebsite';
import Projects from './components/pages/Projects'
import News from './components/pages/newsPage';
import Footer from './components/Footer';

function App() {
  return (
    <React.Fragment>
      <Router basename='np-school-of-engineering/'>
        <ScrollToTop />
        <Navbar />
        <Switch>
          <Route exact path='/' component={Home}/>
          <Route path='/about' component={About}/>
          <Route path='/courses' component={Courses}/>
          <Route path='/studentlife' component={StudentLife}/>
          <Route path='/drone' component={Drone}/>
          <Route path='/projects' component={Projects}/>
          <Route path='/news' component={News}/>
          <Route path='/dronepiloting' component={DronePiloting}/>
          <Route path='/webguide' component={Web}/>
        </Switch>
        <Footer />
      </Router>
      <ScrollTopArrow />
    </React.Fragment>
  );
}

export default App;